$primaryColor: #4527a4;

// Counters
.wp-block-b-blocks-counters {
	* {
		box-sizing: border-box;
	}

	.bBlocksCounters {
		display: grid;

		* {
			box-sizing: border-box;
		}

		>div {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.bBlocksCounter {
			display: flex;
			flex-direction: column;
			justify-content: center;

			.counterIcon {
				margin-bottom: 5px;

				i {
					font-style: inherit;
				}
			}

			.counterNumber {
				margin: 0;
				margin-bottom: 10px;
			}

			.counterLabel {
				margin: 0;
			}

			.counterIconLabel {
				display: inline-flex;
				align-items: center;
				justify-content: center;

				i {
					font-style: inherit;
					margin-right: 10px;
				}
			}
		}
	}
}